import { RC } from "../../lib/helpers/ReactiveComponent";

interface CounterProps {
  delta: number;
}

const CounterComponent = (ctx: RC<CounterProps, HTMLDivElement>) => {
  const { delta } = ctx.useProps();
  const incrementEl = ctx.useChild("increment") as HTMLButtonElement;
  const valueEl = ctx.useChild("value") as HTMLDivElement;

  const countState = ctx.useState({ value: 0 }, () => {
    valueEl.innerHTML = `${countState.value}`;

  });

  incrementEl.addEventListener("click", () => {
    countState.value += delta;
  });
};

export default CounterComponent;
