import { ReactiveComponent } from "./ReactiveComponent";
import Configs from "../configs";

export class ReactiveHtml {
  private components: any;
  private events: any;
  private importer: () => Promise<any> | null;
  private rootElement: any;
  private selector: any;

  constructor({
    components,
    events,
    importer = null,
    rootElement = `[${Configs.root}]`,
    selector = `[${Configs.selector}]`,
  }) {
    this.rootElement = document.body.querySelector(rootElement);
    this.selector = selector;
    this.components = components;
    this.importer = importer;
    this.events = events;

    this.init();
  }

  findComponents(target: HTMLElement) {
    const components = target.querySelectorAll(this.selector);
    return Array.from(components);
  }

  importComponents(target: HTMLElement) {
    return new Promise<any>((resolve, reject) => {
      try {
        const components = this.findComponents(target);
        components.forEach((component) => {
          const componentName = component.dataset.xComponent;
          new ReactiveComponent(component, this.components[componentName]);
        });
        resolve(components);
      } catch (e) {
        reject(e);
      }
    });
  }

  async init() {
    try {
      const res = await this.importComponents(this.rootElement);
      console.log(res);
    } catch (e) {
      console.error(e);
    }
  }
}
